import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

export const relatedCol = ({handleDelete}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
            width: 80,
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 80,
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 200,
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 80,
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 150,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 40,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]

    return array
}