import { Layout, Menu, Image } from 'antd';
import {
    SettingOutlined,
    ContainerOutlined,
    TeamOutlined,
    ExportOutlined,
    HomeOutlined,
    LoginOutlined,
    UserOutlined,
    BankOutlined,
    ReconciliationOutlined
} from '@ant-design/icons';
import Logo from '../asset/srLogo.png'
import { Link } from 'react-router-dom'
import { useContext, useState } from 'react';
import { HeaderData } from '../context/headerContext';
// import { auth } from '../api/firebase';
import { isLoggedInVar } from '../cache';

const { Sider } = Layout;
const { SubMenu } = Menu;

export default function MenuHeader() {
    const { urlPath, user } = useContext(HeaderData)

    const [collapsed, setCollapsed] = useState(false)

    const handleLoggout = ()=>{
        localStorage.removeItem("user")
        isLoggedInVar(false)
    }

    return (
        <Sider collapsible collapsed={collapsed} onCollapse={() => setCollapsed(!collapsed)}
            style={{
                // backgroundColor: '#06385f' 
                // backgroundColor: '#22252A' 
            }}>
            <Link to="/">
                <div className="logo">
                    <Image width={"100%"} src={Logo} preview={false} />
                </div>
            </Link>
            <Menu theme="dark"
                // defaultSelectedKeys={[urlPath]} 
                selectedKeys={[urlPath]}
                mode="inline"
                style={{
                    // backgroundColor: '#06385f'
                    // backgroundColor: '#22252A' 
                }}>
                <Menu.Item key="/" icon={<HomeOutlined />}>
                    
                    <Link to="/" />
                </Menu.Item>
                <Menu.Item key="/case" icon={<LoginOutlined />}>
                    
                    <Link to="/case" />
                </Menu.Item>
                <Menu.Item key="/people" icon={<TeamOutlined />}>
                    
                    <Link to="/people" />
                </Menu.Item>
                <Menu.Item key="/quarantine" icon={<BankOutlined />}>
                    
                    <Link to="/quarantine" />
                </Menu.Item>
                <Menu.Item key="/hospital" icon={<ReconciliationOutlined />}>
                    
                    <Link to="/hospital" />
                </Menu.Item>
                <SubMenu key="sub1" icon={<ContainerOutlined />} title="">
                    <Menu.Item key="/reportdaily">
                        
                        <Link to="/reportdaily" />
                    </Menu.Item>
                    {/* <Menu.Item key="5">
                        
                        <Link to="/reportall" />
                    </Menu.Item> */}
                </SubMenu>
                {/* <Menu.Item key="/setting" icon={<SettingOutlined />}>
                    
                    <Link to="/setting" />
                </Menu.Item> */}
                {user.role === "Admin" ? (
                    <Menu.Item key="/user" icon={<UserOutlined />}>
                        
                        <Link to="/user" />
                    </Menu.Item>
                ) : (
                    <></>
                )}

                <Menu.Item
                    // onClick={() => auth.signOut()}
                    key="6"
                    icon={<ExportOutlined />}
                    onClick={() => handleLoggout()}
                >
                    
                </Menu.Item>
            </Menu>
        </Sider>
    )
}