import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined
} from '@ant-design/icons';

export const subHospitalCol = ({handleDelete, handleEditSubHospital}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 50,
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 20,
            // render: (text, record) => (
            //     <Space size="middle">
            //         {moment(record.date).format("DD MM YYYY")}
            //     </Space>
            // ),
        },

        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                    {record.village + "," + record.commune + "," + record.district + "," + record.province}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 50,
        },
        {
            title: '',
            dataIndex: 'relatedInfo',
            key: 'relatedInfo',
            width: 50,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 20,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <span className="link" onClick={() => handleEditSubHospital(record)}><EditOutlined /></span>
                    <Popconfirm
                        title="?"
                        onConfirm={() => {handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}